import { useState, useCallback, useMemo } from 'react'
import { BarChart, Bar, LineChart, Line, PieChart, Pie, Cell, XAxis, YAxis, CartesianGrid, Tooltip, Legend, ResponsiveContainer } from 'recharts'

const COLORS = ['#3b82f6', '#10b981', '#f59e0b', '#ef4444', '#8b5cf6', '#ec4899']

//  
const reportTemplates = [
  { id: 1, name: '  ', description: '    ', category: '' },
  { id: 2, name: ' ', description: '   ', category: '' },
  { id: 3, name: ' ', description: '    ', category: '' },
  { id: 4, name: 'TOP 10 ', description: ' 10 / ', category: '' },
  { id: 5, name: ' ', description: ' 3  ', category: '' },
  { id: 6, name: ' ', description: '  ', category: '' },
]

//  
const sampleChartData = [
  { name: '1', value: 142234567 },
  { name: '2', value: 148234567 },
  { name: '3', value: 156234567 },
  { name: '4', value: 163456789 },
  { name: '5', value: 158234567 },
  { name: '6', value: 171234567 },
]

const categoryData = [
  { name: 'B2C ', value: 94098050, percent: 52.18 },
  { name: '', value: 44185170, percent: 24.51 },
  { name: '', value: 20700000, percent: 11.48 },
  { name: '', value: 3965833, percent: 2.20 },
  { name: '', value: 17322020, percent: 9.63 },
]

export default function StatisticsCustomReport() {
  const [selectedTemplate, setSelectedTemplate] = useState(null)
  const [reportConfig, setReportConfig] = useState({
    title: '',
    period: 'monthly',
    startDate: '',
    endDate: '',
    includeChart: true,
    includeTable: true,
    chartType: 'bar',
    groupBy: 'category',
    filters: {
      category: [],
      client: [],
      minAmount: '',
      maxAmount: '',
    }
  })
  const [previewMode, setPreviewMode] = useState(false)

  // formatCurrency useCallback 
  const formatCurrency = useCallback((value) => {
    return new Intl.NumberFormat('ko-KR', {
      style: 'currency',
      currency: 'KRW',
    }).format(value)
  }, [])

  //   useCallback 
  const handleTemplateSelect = useCallback((template) => {
    setSelectedTemplate(template)
    setReportConfig(prev => ({
      ...prev,
      title: template.name,
    }))
  }, [])

  const handleGenerateReport = useCallback(() => {
    setPreviewMode(true)
  }, [])

  const handleExport = useCallback((format) => {
    alert(`${format}   .`)
  }, [])

  const handleConfigChange = useCallback((field, value) => {
    setReportConfig(prev => ({
      ...prev,
      [field]: value
    }))
  }, [])

  const handleFilterChange = useCallback((filterField, value) => {
    setReportConfig(prev => ({
      ...prev,
      filters: {
        ...prev.filters,
        [filterField]: value
      }
    }))
  }, [])

  return (
    <div>
      <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
        {/* :   */}
        <div className="lg:col-span-1 space-y-6">
          {/*   */}
          <div className="bg-white rounded-lg shadow-sm border border-gray-200 p-4">
            <h3 className="text-lg font-semibold text-gray-900 mb-4">  </h3>
            <div className="space-y-2">
              {reportTemplates.map((template) => (
                <button
                  key={template.id}
                  onClick={() => handleTemplateSelect(template)}
                  className={`w-full text-left p-3 rounded-lg border transition-all ${
                    selectedTemplate?.id === template.id
                      ? 'border-blue-500 bg-blue-50'
                      : 'border-gray-200 hover:border-gray-300 hover:bg-gray-50'
                  }`}
                >
                  <div className="font-medium text-sm text-gray-900">{template.name}</div>
                  <div className="text-xs text-gray-500 mt-1">{template.description}</div>
                  <div className="text-xs text-blue-600 mt-1">#{template.category}</div>
                </button>
              ))}
            </div>
          </div>

          {/*   */}
          <div className="bg-white rounded-lg shadow-sm border border-gray-200 p-4">
            <h3 className="text-lg font-semibold text-gray-900 mb-4">  </h3>
            
            <div className="space-y-4">
              {/*   */}
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1"> </label>
                <input
                  type="text"
                  value={reportConfig.title}
                  onChange={(e) => handleConfigChange('title', e.target.value)}
                  placeholder="  "
                  className="w-full border border-gray-300 rounded-md px-3 py-2 text-sm"
                />
              </div>

              {/*   */}
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1"></label>
                <select 
                  value={reportConfig.period}
                  onChange={(e) => handleConfigChange('period', e.target.value)}
                  className="w-full border border-gray-300 rounded-md px-3 py-2 text-sm"
                >
                  <option value="daily"></option>
                  <option value="weekly"></option>
                  <option value="monthly"></option>
                  <option value="quarterly"></option>
                  <option value="yearly"></option>
                  <option value="custom"> </option>
                </select>
              </div>

              {/*   */}
              {reportConfig.period === 'custom' && (
                <div className="grid grid-cols-2 gap-2">
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-1"></label>
                    <input
                      type="date"
                      value={reportConfig.startDate}
                      onChange={(e) => handleConfigChange('startDate', e.target.value)}
                      className="w-full border border-gray-300 rounded-md px-3 py-2 text-sm"
                    />
                  </div>
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-1"></label>
                    <input
                      type="date"
                      value={reportConfig.endDate}
                      onChange={(e) => handleConfigChange('endDate', e.target.value)}
                      className="w-full border border-gray-300 rounded-md px-3 py-2 text-sm"
                    />
                  </div>
                </div>
              )}

              {/*   */}
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1"> </label>
                <select 
                  value={reportConfig.groupBy}
                  onChange={(e) => handleConfigChange('groupBy', e.target.value)}
                  className="w-full border border-gray-300 rounded-md px-3 py-2 text-sm"
                >
                  <option value="category"></option>
                  <option value="client"></option>
                  <option value="item"></option>
                  <option value="date"></option>
                </select>
              </div>

              {/*   */}
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1"> </label>
                <select 
                  value={reportConfig.chartType}
                  onChange={(e) => handleConfigChange('chartType', e.target.value)}
                  className="w-full border border-gray-300 rounded-md px-3 py-2 text-sm"
                >
                  <option value="bar"> </option>
                  <option value="line"> </option>
                  <option value="pie"> </option>
                  <option value="area"> </option>
                </select>
              </div>

              {/*   */}
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2"> </label>
                <div className="space-y-2">
                  <label className="flex items-center">
                    <input
                      type="checkbox"
                      checked={reportConfig.includeChart}
                      onChange={(e) => handleConfigChange('includeChart', e.target.checked)}
                      className="rounded border-gray-300"
                    />
                    <span className="ml-2 text-sm text-gray-700"> </span>
                  </label>
                  <label className="flex items-center">
                    <input
                      type="checkbox"
                      checked={reportConfig.includeTable}
                      onChange={(e) => handleConfigChange('includeTable', e.target.checked)}
                      className="rounded border-gray-300"
                    />
                    <span className="ml-2 text-sm text-gray-700"> </span>
                  </label>
                </div>
              </div>

              {/*   */}
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1"> </label>
                <div className="grid grid-cols-2 gap-2">
                  <input
                    type="number"
                    placeholder=""
                    value={reportConfig.filters.minAmount}
                    onChange={(e) => handleFilterChange('minAmount', e.target.value)}
                    className="border border-gray-300 rounded-md px-3 py-2 text-sm"
                  />
                  <input
                    type="number"
                    placeholder=""
                    value={reportConfig.filters.maxAmount}
                    onChange={(e) => handleFilterChange('maxAmount', e.target.value)}
                    className="border border-gray-300 rounded-md px-3 py-2 text-sm"
                  />
                </div>
              </div>
            </div>

            {/*   */}
            <div className="mt-6 space-y-2">
              <button
                onClick={handleGenerateReport}
                className="w-full bg-blue-600 text-white px-4 py-2 rounded-md text-sm hover:bg-blue-700"
              >
                 
              </button>
              <div className="grid grid-cols-3 gap-2">
                <button
                  onClick={() => handleExport('PDF')}
                  className="bg-red-600 text-white px-3 py-2 rounded-md text-xs hover:bg-red-700"
                >
                   PDF
                </button>
                <button
                  onClick={() => handleExport('Excel')}
                  className="bg-green-600 text-white px-3 py-2 rounded-md text-xs hover:bg-green-700"
                >
                   Excel
                </button>
                <button
                  onClick={() => handleExport('CSV')}
                  className="bg-gray-600 text-white px-3 py-2 rounded-md text-xs hover:bg-gray-700"
                >
                   CSV
                </button>
              </div>
            </div>
          </div>
        </div>

        {/* :   */}
        <div className="lg:col-span-2">
          <div className="bg-white rounded-lg shadow-sm border border-gray-200">
            {/*   */}
            <div className="px-6 py-4 border-b border-gray-200 bg-gray-50">
              <div className="flex items-center justify-between">
                <div>
                  <h2 className="text-xl font-bold text-gray-900">
                    {reportConfig.title || ' '}
                  </h2>
                  <p className="text-sm text-gray-500 mt-1">
                    : {new Date().toLocaleDateString('ko-KR')} | 
                    : {reportConfig.period === 'monthly' ? '' : reportConfig.period}
                  </p>
                </div>
                {previewMode && (
                  <button
                    onClick={() => setPreviewMode(false)}
                    className="text-gray-500 hover:text-gray-700"
                  >
                    
                  </button>
                )}
              </div>
            </div>

            {/*   */}
            <div className="p-6">
              {!previewMode ? (
                <div className="text-center py-12">
                  <div className="text-6xl mb-4"></div>
                  <p className="text-gray-500 text-lg">
                      <br />
                         
                  </p>
                </div>
              ) : (
                <div className="space-y-6">
                  {/*   */}
                  <div className="grid grid-cols-3 gap-4">
                    <div className="bg-blue-50 border border-blue-200 rounded-lg p-4">
                      <div className="text-sm text-blue-700 mb-1"> </div>
                      <div className="text-2xl font-bold text-blue-900">180.3</div>
                    </div>
                    <div className="bg-green-50 border border-green-200 rounded-lg p-4">
                      <div className="text-sm text-green-700 mb-1"> </div>
                      <div className="text-2xl font-bold text-green-900">9</div>
                    </div>
                    <div className="bg-orange-50 border border-orange-200 rounded-lg p-4">
                      <div className="text-sm text-orange-700 mb-1"></div>
                      <div className="text-2xl font-bold text-orange-900">20.0</div>
                    </div>
                  </div>

                  {/*  */}
                  {reportConfig.includeChart && (
                    <div className="border border-gray-200 rounded-lg p-4">
                      <h3 className="text-lg font-semibold text-gray-900 mb-4">
                        {reportConfig.chartType === 'pie' ? ' ' : ' '}
                      </h3>
                      <ResponsiveContainer width="100%" height={300}>
                        {reportConfig.chartType === 'bar' ? (
                          <BarChart data={sampleChartData}>
                            <CartesianGrid strokeDasharray="3 3" />
                            <XAxis dataKey="name" fontSize={12} />
                            <YAxis fontSize={12} />
                            <Tooltip formatter={(value) => formatCurrency(value)} />
                            <Bar dataKey="value" fill="#3b82f6" />
                          </BarChart>
                        ) : reportConfig.chartType === 'line' ? (
                          <LineChart data={sampleChartData}>
                            <CartesianGrid strokeDasharray="3 3" />
                            <XAxis dataKey="name" fontSize={12} />
                            <YAxis fontSize={12} />
                            <Tooltip formatter={(value) => formatCurrency(value)} />
                            <Line type="monotone" dataKey="value" stroke="#3b82f6" strokeWidth={2} />
                          </LineChart>
                        ) : (
                          <PieChart>
                            <Pie
                              data={categoryData}
                              cx="50%"
                              cy="50%"
                              labelLine={false}
                              label={({ name, percent }) => `${name} ${percent}%`}
                              outerRadius={100}
                              fill="#8884d8"
                              dataKey="value"
                            >
                              {categoryData.map((entry, index) => (
                                <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
                              ))}
                            </Pie>
                            <Tooltip formatter={(value) => formatCurrency(value)} />
                          </PieChart>
                        )}
                      </ResponsiveContainer>
                    </div>
                  )}

                  {/*  */}
                  {reportConfig.includeTable && (
                    <div className="border border-gray-200 rounded-lg overflow-hidden">
                      <table className="w-full">
                        <thead className="bg-gray-50">
                          <tr>
                            <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase">
                              
                            </th>
                            <th className="px-4 py-3 text-right text-xs font-medium text-gray-500 uppercase">
                              
                            </th>
                            <th className="px-4 py-3 text-right text-xs font-medium text-gray-500 uppercase">
                              
                            </th>
                          </tr>
                        </thead>
                        <tbody className="divide-y divide-gray-200">
                          {categoryData.map((item, index) => (
                            <tr key={index}>
                              <td className="px-4 py-3 text-sm text-gray-900">{item.name}</td>
                              <td className="px-4 py-3 text-sm text-right text-gray-900">
                                {formatCurrency(item.value)}
                              </td>
                              <td className="px-4 py-3 text-sm text-right text-gray-600">
                                {item.percent}%
                              </td>
                            </tr>
                          ))}
                        </tbody>
                      </table>
                    </div>
                  )}

                  {/*   */}
                  <div className="border-t border-gray-200 pt-4 text-sm text-gray-500">
                    <p>  KREAM ERP   .</p>
                    <p className="mt-1">: admin@kream.co.kr</p>
                  </div>
                </div>
              )}
            </div>
          </div>

          {/*   */}
          <div className="mt-6 bg-white rounded-lg shadow-sm border border-gray-200 p-4">
            <h3 className="text-lg font-semibold text-gray-900 mb-4">  </h3>
            <div className="space-y-2">
              {[
                { name: '9   ', date: '2024-09-30', size: '2.4MB' },
                { name: '3  ', date: '2024-09-28', size: '1.8MB' },
                { name: '8 TOP 10 ', date: '2024-08-31', size: '1.2MB' },
              ].map((report, index) => (
                <div key={index} className="flex items-center justify-between p-3 border border-gray-200 rounded-lg hover:bg-gray-50">
                  <div className="flex items-center gap-3">
                    <div className="text-2xl"></div>
                    <div>
                      <div className="text-sm font-medium text-gray-900">{report.name}</div>
                      <div className="text-xs text-gray-500">{report.date}  {report.size}</div>
                    </div>
                  </div>
                  <button className="text-blue-600 hover:text-blue-700 text-sm font-medium">
                    
                  </button>
                </div>
              ))}
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

