import { useState } from 'react'
import { SearchButton, ResetButton, AddButton, DownloadButton, EditButton, DeleteButton } from '../components/Button'

export default function MasterFeeCode() {
  const [searchTerm, setSearchTerm] = useState('')
  const [feeType, setFeeType] = useState('all')

  const dummyData = [
    { id: 1, feeCode: 'F001', feeName: '', feeType: '', feeRate: '10%', feeAmount: null, applyTarget: ' ', status: '', createdAt: '2024-01-10', updatedAt: '2024-11-01' },
    { id: 2, feeCode: 'F002', feeName: '', feeType: '', feeRate: null, feeAmount: 3000, applyTarget: ' ', status: '', createdAt: '2024-01-10', updatedAt: '2024-10-15' },
    { id: 3, feeCode: 'F003', feeName: 'VIP ', feeType: '', feeRate: '5%', feeAmount: null, applyTarget: 'VIP ', status: '', createdAt: '2024-02-01', updatedAt: '2024-11-10' },
    { id: 4, feeCode: 'F004', feeName: ' ', feeType: '', feeRate: null, feeAmount: 15000, applyTarget: ' ', status: '', createdAt: '2024-03-05', updatedAt: '2024-09-20' },
  ]

  const handleSearch = () => {
    console.log(':', searchTerm, feeType)
  }

  return (
    <div className="space-y-6">
      {/*    */}
      <div className="bg-white rounded-lg border border-gray-200">
        <div className="p-6">
          <div className="grid grid-cols-12 gap-4 items-end">
            <div className="col-span-3">
              <label className="block text-xs text-gray-600 mb-2"></label>
              <input
                type="text"
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                placeholder=",  "
                className="w-full px-3 py-2 text-sm border border-gray-300 rounded focus:outline-none focus:ring-2 focus:ring-blue-500"
              />
            </div>
            <div className="col-span-2">
              <label className="block text-xs text-gray-600 mb-2"> </label>
              <select
                value={feeType}
                onChange={(e) => setFeeType(e.target.value)}
                className="w-full px-3 py-2 text-sm border border-gray-300 rounded focus:outline-none focus:ring-2 focus:ring-blue-500"
              >
                <option value="all"></option>
                <option value="rate"></option>
                <option value="fixed"></option>
              </select>
            </div>
            <div className="col-span-7 flex gap-2 justify-end">
              <SearchButton onClick={handleSearch} />
              <ResetButton />
            </div>
          </div>
        </div>
      </div>

      {/*   */}
      <div className="bg-white rounded-lg border border-gray-200">
        <div className="p-4 border-b border-gray-200 flex items-center justify-between">
          <div className="text-sm text-gray-600">
             <span className="font-semibold text-gray-900">{dummyData.length}</span>
          </div>
          <div className="flex gap-2">
            <AddButton />
            <DownloadButton />
          </div>
        </div>
        <div className="overflow-x-auto">
          <table className="min-w-full divide-y divide-gray-200">
            <thead className="bg-gray-50">
              <tr>
                <th className="px-4 py-3 text-left text-xs font-semibold text-gray-700">
                  <input type="checkbox" className="rounded border-gray-300" />
                </th>
                <th className="px-4 py-3 text-left text-xs font-semibold text-gray-700"> </th>
                <th className="px-4 py-3 text-left text-xs font-semibold text-gray-700"></th>
                <th className="px-4 py-3 text-center text-xs font-semibold text-gray-700"></th>
                <th className="px-4 py-3 text-right text-xs font-semibold text-gray-700"></th>
                <th className="px-4 py-3 text-right text-xs font-semibold text-gray-700"></th>
                <th className="px-4 py-3 text-center text-xs font-semibold text-gray-700"> </th>
                <th className="px-4 py-3 text-center text-xs font-semibold text-gray-700"></th>
                <th className="px-4 py-3 text-center text-xs font-semibold text-gray-700"></th>
                <th className="px-4 py-3 text-center text-xs font-semibold text-gray-700"></th>
                <th className="px-4 py-3 text-center text-xs font-semibold text-gray-700"></th>
              </tr>
            </thead>
            <tbody className="divide-y divide-gray-200 bg-white">
              {dummyData.map((item) => (
                <tr key={item.id} className="hover:bg-gray-50 transition-colors">
                  <td className="px-4 py-3">
                    <input type="checkbox" className="rounded border-gray-300" />
                  </td>
                  <td className="px-4 py-3">
                    <span className="text-sm font-mono font-medium text-blue-600">{item.feeCode}</span>
                  </td>
                  <td className="px-4 py-3">
                    <span className="text-sm font-medium text-gray-900">{item.feeName}</span>
                  </td>
                  <td className="px-4 py-3 text-center">
                    <span className={`inline-flex px-2 py-1 text-xs font-medium rounded-full ${
                      item.feeType === '' ? 'bg-blue-100 text-blue-700' : 'bg-green-100 text-green-700'
                    }`}>
                      {item.feeType}
                    </span>
                  </td>
                  <td className="px-4 py-3 text-right">
                    <span className="text-sm text-gray-900">{item.feeRate || '-'}</span>
                  </td>
                  <td className="px-4 py-3 text-right">
                    <span className="text-sm text-gray-900">
                      {item.feeAmount ? `${item.feeAmount.toLocaleString()}` : '-'}
                    </span>
                  </td>
                  <td className="px-4 py-3 text-center">
                    <span className="text-sm text-gray-600">{item.applyTarget}</span>
                  </td>
                  <td className="px-4 py-3 text-center">
                    <span className={`inline-flex px-2 py-1 text-xs font-medium rounded-full ${
                      item.status === '' ? 'bg-green-100 text-green-700' : 'bg-red-100 text-red-700'
                    }`}>
                      {item.status}
                    </span>
                  </td>
                  <td className="px-4 py-3 text-center">
                    <span className="text-xs text-gray-500">{item.createdAt}</span>
                  </td>
                  <td className="px-4 py-3 text-center">
                    <span className="text-xs text-gray-500">{item.updatedAt}</span>
                  </td>
                  <td className="px-4 py-3 text-center">
                    <div className="flex items-center justify-center gap-2">
                      <EditButton />
                      <DeleteButton />
                    </div>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </div>
    </div>
  )
}

