export default function Dashboard() {
  return (
    <div>
      {/*   */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4 mb-8">
        {[
          { label: ' ', value: '1,234', unit: '', change: '+15.3%', isPositive: true },
          { label: ' ', value: '5.2', unit: '', change: '+8.7%', isPositive: true },
          { label: ' ', value: '45', unit: '', change: '0%', isPositive: null },
          { label: ' ', value: '12', unit: '', change: '-25.0%', isPositive: false },
        ].map((card, idx) => (
          <div key={idx} className="card-hover">
            <div className="text-sm text-kream-gray-600 mb-2">{card.label}</div>
            <div className="text-4xl font-bold mb-2">
              {card.value}<span className="text-xl ml-1">{card.unit}</span>
            </div>
            <div className={`text-xs ${card.isPositive === true ? 'text-success' : card.isPositive === false ? 'text-danger' : 'text-kream-gray-500'}`}>
              {card.change}  
            </div>
          </div>
        ))}
      </div>

      {/*   */}
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        <div className="card">
          <h2 className="text-lg font-bold mb-4"> </h2>
          <div className="space-y-2">
            <button className="btn btn-primary w-full"> </button>
            <button className="btn btn-secondary w-full"> </button>
            <button className="btn btn-secondary w-full">  </button>
          </div>
        </div>
        <div className="card">
          <h2 className="text-lg font-bold mb-4"> </h2>
          <div className="space-y-3 text-sm">
            <div className="flex items-start gap-2">
              <span className="text-warning"></span>
              <div>
                <p className="font-medium">  </p>
                <p className="text-kream-gray-600 text-xs"> "" -  </p>
              </div>
            </div>
            <div className="flex items-start gap-2">
              <span className="text-success"></span>
              <div>
                <p className="font-medium">  </p>
                <p className="text-kream-gray-600 text-xs">STMT-20250115-001</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

