import { useState, useEffect } from 'react'

export default function SettlementList() {
  const [filters, setFilters] = useState({
    category: '',
    apiId: '',
    status: '',
    dateRange: '',
    startDate: '',
    endDate: ''
  })
  const [activePeriod, setActivePeriod] = useState('thismonth')

  //   
  const formatDate = (date) => {
    const y = date.getFullYear()
    const m = String(date.getMonth() + 1).padStart(2, '0')
    const d = String(date.getDate()).padStart(2, '0')
    return `${y}-${m}-${d}`
  }

  //   
  const setDateRange = (period) => {
    const today = new Date()
    let start = new Date()
    let end = new Date()

    switch(period) {
      case 'today':
        start = today
        end = today
        break
      case 'thismonth':
        start = new Date(today.getFullYear(), today.getMonth(), 1)
        end = today
        break
      case 'lastmonth':
        start = new Date(today.getFullYear(), today.getMonth() - 1, 1)
        end = new Date(today.getFullYear(), today.getMonth(), 0)
        break
      case '1month':
        start = new Date(today.setMonth(today.getMonth() - 1))
        end = new Date()
        break
      case '1year':
        start = new Date(today.setFullYear(today.getFullYear() - 1))
        end = new Date()
        break
    }

    setFilters({
      ...filters,
      startDate: formatDate(start),
      endDate: formatDate(end)
    })
    setActivePeriod(period)
  }

  //   
  useEffect(() => {
    const today = new Date()
    const start = new Date(today.getFullYear(), today.getMonth(), 1)
    const end = today
    
    setFilters(prev => ({
      ...prev,
      startDate: formatDate(start),
      endDate: formatDate(end)
    }))
    setActivePeriod('thismonth')
  }, [])

  //   
  const handleResetFilters = () => {
    setFilters({
      category: '',
      apiId: '',
      status: '',
      dateRange: '',
      startDate: '',
      endDate: ''
    })
    setActivePeriod('')
  }

  // API 
  const handleSearch = () => {
    console.log(' :', filters)
    // TODO:  API   
  }

  return (
    <div className="space-y-6">
      {/*    */}
      <div className="bg-white rounded-lg shadow-sm border border-gray-200 p-6">
        <form className="flex flex-nowrap items-center gap-4" onSubmit={(e) => { e.preventDefault(); handleSearch(); }}>
          {/*  Select */}
          <div className="floating-wrapper w-40 flex-shrink-0">
            <select 
              id="category" 
              className={`floating-input ${filters.category ? 'has-value' : ''}`}
              value={filters.category}
              onChange={(e) => setFilters({...filters, category: e.target.value})}
            >
              <option value="" disabled></option>
              <option value="Qno010 JP">Qno010 JP</option>
              <option value="Qno020 KR">Qno020 KR</option>
              <option value="Qno030 US">Qno030 US</option>
            </select>
            <label className="floating-label"></label>
            {filters.category && (
              <span 
                className="select-clear" 
                onClick={() => setFilters({...filters, category: ''})}
              ></span>
            )}
          </div>

          {/* API ID Select */}
          <div className="floating-wrapper w-40 flex-shrink-0">
            <select 
              id="apiId" 
              className={`floating-input ${filters.apiId ? 'has-value' : ''}`}
              value={filters.apiId}
              onChange={(e) => setFilters({...filters, apiId: e.target.value})}
            >
              <option value="" disabled></option>
              <option value="API001">API001</option>
              <option value="API002">API002</option>
              <option value="API003">API003</option>
            </select>
            <label className="floating-label">API ID</label>
            {filters.apiId && (
              <span 
                className="select-clear" 
                onClick={() => setFilters({...filters, apiId: ''})}
              ></span>
            )}
          </div>

          {/*  Select */}
          <div className="floating-wrapper w-40 flex-shrink-0">
            <select 
              id="status" 
              className={`floating-input ${filters.status ? 'has-value' : ''}`}
              value={filters.status}
              onChange={(e) => setFilters({...filters, status: e.target.value})}
            >
              <option value="" disabled></option>
              <option value=""></option>
              <option value=""></option>
              <option value=""></option>
              <option value=""></option>
            </select>
            <label className="floating-label"></label>
            {filters.status && (
              <span 
                className="select-clear" 
                onClick={() => setFilters({...filters, status: ''})}
              ></span>
            )}
          </div>

          {/* Date Range */}
          <div className="flex items-center gap-2 flex-shrink-0">
            {/*  */}
            <div className="floating-wrapper date-wrapper w-40">
              <input 
                id="start-date" 
                type="date" 
                className={`floating-input pr-10 ${filters.startDate ? 'has-value' : ''}`}
                value={filters.startDate}
                onChange={(e) => setFilters({...filters, startDate: e.target.value})}
              />
              <label className="floating-label"></label>
              <svg className="date-icon w-5 h-5" fill="currentColor" viewBox="0 0 20 20">
                <path fillRule="evenodd" d="M6 2a1 1 0 00-1 1v1H4a2 2 0 00-2 2v10a2 2 0 002 2h12a2 2 0 002-2V6a2 2 0 00-2-2h-1V3a1 1 0 10-2 0v1H7V3a1 1 0 00-1-1zm0 5a1 1 0 000 2h8a1 1 0 100-2H6z" clipRule="evenodd" />
              </svg>
            </div>

            <span className="text-gray-400">~</span>

            {/*  */}
            <div className="floating-wrapper date-wrapper w-40">
              <input 
                id="end-date" 
                type="date" 
                className={`floating-input pr-10 ${filters.endDate ? 'has-value' : ''}`}
                value={filters.endDate}
                onChange={(e) => setFilters({...filters, endDate: e.target.value})}
              />
              <label className="floating-label"></label>
              <svg className="date-icon w-5 h-5" fill="currentColor" viewBox="0 0 20 20">
                <path fillRule="evenodd" d="M6 2a1 1 0 00-1 1v1H4a2 2 0 00-2 2v10a2 2 0 002 2h12a2 2 0 002-2V6a2 2 0 00-2-2h-1V3a1 1 0 10-2 0v1H7V3a1 1 0 00-1-1zm0 5a1 1 0 000 2h8a1 1 0 100-2H6z" clipRule="evenodd" />
              </svg>
            </div>
          </div>

          {/*  */}
          <div className="h-8 w-px bg-gray-300 flex-shrink-0"></div>

          {/*   */}
          <div className="flex items-center gap-2 flex-shrink-0">
            <button 
              type="button" 
              className={`quick-btn ${activePeriod === '1year' ? 'active' : ''}`}
              onClick={() => setDateRange('1year')}
            >
              1
            </button>
            <button 
              type="button" 
              className={`quick-btn ${activePeriod === '1month' ? 'active' : ''}`}
              onClick={() => setDateRange('1month')}
            >
              
            </button>
            <button 
              type="button" 
              className={`quick-btn ${activePeriod === 'lastmonth' ? 'active' : ''}`}
              onClick={() => setDateRange('lastmonth')}
            >
              
            </button>
            <button 
              type="button" 
              className={`quick-btn ${activePeriod === 'thismonth' ? 'active' : ''}`}
              onClick={() => setDateRange('thismonth')}
            >
              
            </button>
            <button 
              type="button" 
              className={`quick-btn ${activePeriod === 'today' ? 'active' : ''}`}
              onClick={() => setDateRange('today')}
            >
              
            </button>
          </div>

          {/*   */}
          <div className="ml-auto flex gap-3">
            <button 
              type="button" 
              className="action-btn btn-reset"
              onClick={handleResetFilters}
            >
              
            </button>
            <button 
              type="submit" 
              className="action-btn btn-submit"
            >
              API 
            </button>
          </div>
        </form>
      </div>

      <style jsx>{`
        /*    */
        .floating-wrapper {
          position: relative;
          display: inline-block;
        }

        /* Input/Select   */
        .floating-input {
          width: 100%;
          height: 44px;
          padding: 12px 12px 4px;
          border: 1px solid #D1D5DB;
          border-radius: 8px;
          font-size: 14px;
          color: #111827;
          background: white;
          transition: all 0.2s;
        }

        /* Select    */
        select.floating-input {
          padding-right: 36px;
          background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' fill='none' viewBox='0 0 20 20'%3e%3cpath stroke='%236b7280' stroke-linecap='round' stroke-linejoin='round' stroke-width='1.5' d='M6 8l4 4 4-4'/%3e%3c/svg%3e");
          background-repeat: no-repeat;
          background-position: right 10px center;
          background-size: 1.5em;
          appearance: none;
          -webkit-appearance: none;
          -moz-appearance: none;
        }
        
        /* IE    */
        select.floating-input::-ms-expand {
          display: none;
        }
        
        /*    */
        select.floating-input option {
          padding: 8px 12px;
          border: 1px solid #E5E7EB;
        }

        /* Select    X     */
        select.floating-input.has-value {
          padding-right: 60px;
        }

        .floating-input:focus {
          outline: none;
          border-color: #3B82F6;
          box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
        }

        /* Select   */
        .select-clear {
          position: absolute;
          right: 32px;
          top: 50%;
          transform: translateY(-50%);
          width: 18px;
          height: 18px;
          background: #9CA3AF;
          border-radius: 50%;
          display: flex;
          align-items: center;
          justify-content: center;
          cursor: pointer;
          transition: background 0.2s;
        }

        .select-clear:hover {
          background: #6B7280;
        }

        .select-clear::before,
        .select-clear::after {
          content: '';
          position: absolute;
          width: 10px;
          height: 2px;
          background: white;
        }

        .select-clear::before {
          transform: rotate(45deg);
        }

        .select-clear::after {
          transform: rotate(-45deg);
        }

        /*    -   */
        .floating-label {
          position: absolute;
          left: 12px;
          top: 50%;
          transform: translateY(-50%);
          font-size: 14px;
          color: #9CA3AF;
          pointer-events: none;
          transition: all 0.2s ease;
          background: white;
          padding: 0 4px;
        }

        /* Date input    (   ) */
        input[type="date"] + .floating-label {
          top: -8px;
          transform: translateY(0);
          font-size: 12px;
          font-weight: 500;
          color: #6B7280;
        }

        /*        */
        .floating-input:focus + .floating-label,
        .floating-input.has-value + .floating-label {
          top: -8px;
          transform: translateY(0);
          font-size: 12px;
          font-weight: 500;
          color: #6B7280;
        }

        .floating-input:focus + .floating-label {
          color: #3B82F6;
        }

        /* Date input  */
        .date-icon {
          position: absolute;
          right: 12px;
          top: 50%;
          transform: translateY(-50%);
          pointer-events: none;
          color: #9CA3AF;
        }

        input[type="date"]::-webkit-calendar-picker-indicator {
          position: absolute;
          inset: 0;
          opacity: 0;
          cursor: pointer;
        }

        /*    */
        .quick-btn {
          height: 44px;
          padding: 0 20px;
          font-size: 14px;
          font-weight: 600;
          border-radius: 8px;
          transition: all 0.15s;
          cursor: pointer;
          display: flex;
          align-items: center;
          justify-content: center;
        }

        .quick-btn.active {
          background: #111827;
          color: white;
          border: 1px solid #111827;
        }

        .quick-btn:not(.active) {
          background: white;
          color: #111827;
          border: 1px solid #D1D5DB;
        }

        .quick-btn:not(.active):hover {
          background: #111827;
          color: white;
          border-color: #111827;
        }

        .quick-btn.active:hover {
          background: #374151;
          border-color: #374151;
        }

        /*    */
        .action-btn {
          height: 44px;
          padding: 0 24px;
          font-size: 14px;
          font-weight: 600;
          border-radius: 8px;
          transition: all 0.2s;
          cursor: pointer;
          display: flex;
          align-items: center;
          justify-content: center;
          white-space: nowrap;
        }

        .btn-reset {
          background: white;
          border: 1px solid #D1D5DB;
          color: #6B7280;
        }

        .btn-reset:hover {
          background: #F9FAFB;
          border-color: #9CA3AF;
        }

        .btn-submit {
          background: #3B82F6;
          border: none;
          color: white;
        }

        .btn-submit:hover {
          background: #2563EB;
        }
      `}</style>


      {/*    */}
      <div className="bg-white rounded-lg border border-gray-200">
        <div className="p-6">
          <div className="text-center py-12 text-gray-500">
            <svg className="w-16 h-16 mx-auto mb-4 text-gray-300" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={1.5} d="M9 12h6m-6 4h6m2 5H7a2 2 0 01-2-2V5a2 2 0 012-2h5.586a1 1 0 01.707.293l5.414 5.414a1 1 0 01.293.707V19a2 2 0 01-2 2z" />
            </svg>
            <p className="text-sm">   'API '  </p>
          </div>
        </div>
      </div>
    </div>
  )
}

